import axios from "axios";
import { saveAs } from "file-saver";

function getPDF() {
  return axios.get(`https://excellent-capable-vulcanodon.glitch.me/test`, {
    responseType: 'arraybuffer',
    headers: {
      'Accept': 'application/pdf'
    }
  })
}

// Function resposible for downloading PDF from server
let savePDF = () => {
    
  return getPDF() // API call
  .then((response) => {
    if (response.status === 200) {
      console.log("drugi?", response.status);
      const blob = new Blob([response.data], { type: "application/pdf" });
      const link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.download = `dietCalculation.pdf`;
      link.click();

      
    }
  })
  .catch((err) => {} /** error handling **/);
}

export let puppeterPDF = (object) => {
  axios
    .post(
      "https://excellent-capable-vulcanodon.glitch.me/create-pdf",
      object
    )
    .then(()=>{
      savePDF();
    })
}
