import React, { Component } from "react";
import {useMediaQuery} from '@material-ui/core'
import { Navbar,Dropdown } from 'react-bootstrap';
import { Link, NavLink } from "react-router-dom";
export default ()=> {
  
const isActive = useMediaQuery('(max-width: 200px)');
const isActive1 = useMediaQuery('(min-width: 400px) and (max-width: 600px )');
const isActive2 = useMediaQuery('(max-width: 200px)');

console.log(isActive)
    return (
      <div className="container-contact">
        
        <div className="testowe">

          Witaj tutaj strona o mnie :)
          {isActive && <h1>Dziala media querry :)</h1>}
          {isActive1 && <h1>Dziala media querry drugie :)</h1>}
          <div>
          <ul class="nav nav-tabs">
              <NavLink
                exact={true}
                activeClassName="active"
                className="nav-link"
                to="/"
              >
                <li class="nav-item">Dom</li>
              </NavLink>
              <NavLink
                exact={true}
                activeClassName="active"
                className="nav-link"
                to="/calculate"
              >
                <li class="nav-item">Kalkulator</li>
              </NavLink>
              <NavLink
                exact={true}
                activeClassName="active"
                className="nav-link"
                to="/info"
              >
                <li class="nav-item">Informacje o delegacji</li>
              </NavLink>
              <NavLink
                exact={true}
                activeClassName="active"
                className="nav-link"
                to="/miniGame"
              >
                <li class="nav-item">Snake Gra</li>
              </NavLink>
              <NavLink
                exact={true}
                activeClassName="active"
                className="nav-link"
                to="/aboutMe"
              >
                <li class="nav-item">O mnie</li>
              </NavLink>
            </ul>
            
          </div>
          <div>
            

          <Dropdown>
  <Dropdown.Toggle variant="success" id="dropdown-basic">
    Dropdown Button
  </Dropdown.Toggle>

  <Dropdown.Menu>
    <Dropdown.Item href="#/action-1">Action</Dropdown.Item>
    <Dropdown.Item href="#/action-2">Another action</Dropdown.Item>
    <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
  </Dropdown.Menu>
</Dropdown>
            
          </div>

        </div>
      </div>
    );
  
}
